@import '../../__data__/style/colors.module.css';
@import '../../__data__/style/sizes.module.css';
@import '../../__data__/style/media.module.css';

.wrapper {
    margin-top: var(--offset-md);
    display: flex;
    flex-direction: column;
    position: relative;
}

.wrapper .error {
    position: absolute;
    top: -28px;
    font-weight: 300;
    font-size: 18px;
    color: var(--red-5);
    border: 1px solid var(--red-5);
    border-radius: 3px;
    background: var(--yellow-4);
    padding: 0 var(--offset-xs);
    z-index: 2;
}

.wrapper .textarea {
    background-color: var(--white);
    border: 2px solid var(--gray-2);
    border-radius: 3px;
    padding-left: var(--offset-sm);
    padding-right: var(--offset-sm);
    color: var(--blue-6);
}

.wrapper .textarea.danger {
    border: 2px solid var(--red-5);
}

.wrapper .textarea::placeholder {
    font-weight: 300;
    font-size: 18px;
    color: var(--gray-2);
}

.wrapper .textarea:hover {
    border-color: var(--green-4);
    outline: 1px solid var(--green-3);
    box-shadow: 0 0 3pt 2pt var(--green-3);
}

.wrapper .textarea:focus {
    border-color: var(--green-4);
    outline: 1px solid var(--green-3);
    box-shadow: 0 0 3pt 2pt var(--green-3);
}
